<template>
<v-dialog v-model="dialog" persistent max-width="600px">
      <template v-slot:activator="{ on: dialog }">
            <v-tooltip top>
              <template v-slot:activator="{ on: tooltip }"> 
                <v-btn icon dark v-on="{...tooltip, ...dialog}">
                  <v-icon>add</v-icon>
                </v-btn>
              </template>
              <span>Add Schedule</span>
              </v-tooltip>
      </template>

      <v-card>
        <v-card-text>
          <v-container grid-list-sm>
            <v-layout wrap>
              <v-flex xs12>
                <v-card-title>
                  <span class="headline">Create Scheduler</span>
                </v-card-title>
              </v-flex>

        <v-flex lg12 md12 sm12 xs12>
          <div>
            <v-radio-group v-model='scheduler_type' @click='schedulerFrequencyText()'>
              <v-radio default label='Days' value='scheduler'></v-radio>
              <v-radio label='Timer' value='timer'></v-radio>
            </v-radio-group>
          </div>
        </v-flex>

          <v-flex lg7 md7 sm7 xs12 v-if="scheduler_type==='scheduler'">
            <v-time-picker v-model="e4" @input='schedulerFrequencyText()' color="green lighten-1"></v-time-picker>
          </v-flex>

          <v-flex lg5 md5 sm5 xs12 v-if="scheduler_type==='scheduler'">
            <v-layout wrap @click='schedulerFrequencyText()'>
              <v-flex lg6 md6 xs3 sm6 md6>
                <v-checkbox 
                  v-model="checkbox_dates"
                  label="Mon"
                  value="monday">
                </v-checkbox>
              </v-flex>
              <v-flex lg6 xs3 sm6 md6>
                <v-checkbox
                  v-model="checkbox_dates"
                  label="Tue"
                  value="tuesday">
                </v-checkbox>
              </v-flex>
              <v-flex lg6 xs3 sm6 md6>
                <v-checkbox 
                  v-model="checkbox_dates"
                  label="Wed"
                  value="wednesday">
                </v-checkbox>
              </v-flex>
              <v-flex lg6 xs3 sm6 md6>
                <v-checkbox
                  v-model="checkbox_dates"
                  label="Thu"
                  value="thursday">
                </v-checkbox>
              </v-flex>
              <v-flex lg6 xs3 sm12 md6>
                <v-checkbox
                  v-model="checkbox_dates"
                  label="Fri"
                  value="friday">
                </v-checkbox>
              </v-flex>
              <v-flex lg6 xs3 sm12 md6>
                <v-checkbox
                  v-model="checkbox_dates"
                  label="Sat"
                  value="saturday">
                </v-checkbox>
              </v-flex>
              <v-flex lg6 xs3 sm12 md6>
                <v-checkbox
                  v-model="checkbox_dates"
                  label="Sun"
                  value="sunday">
                </v-checkbox>
              </v-flex>

            </v-layout>
        </v-flex>


        <v-flex v-else>
            <v-layout wrap @input='schedulerFrequencyText()'>
              <v-flex lg4 md5 sm5 xs12>
                <v-text-field v-model="timer_freq" mask="######" label='Frequency' placeholder='X' outline></v-text-field>
              </v-flex>

              <v-flex lg4 md5 sm5 xs12>
                <v-select @input='schedulerFrequencyText()' v-model="timer_freq_unit" :items='["minutes", "hours", "days", "weeks"]' label="Frequency unit" outline>
              </v-select>
              </v-flex>
              

              <v-flex @click="schedulerFrequencyText()" lg3 md2 sm2>
                <v-switch label='Repeat'  v-model='timer_repeat'></v-switch>
              </v-flex>

            </v-layout>
        </v-flex>


        <v-flex lg12 md12 sm12 xs12> 
          <v-divider></v-divider><v-divider></v-divider>
          <v-text-field v-model='scheduler_text' readonly solo>
          </v-text-field>
        </v-flex>


            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click="dialog = false">Cancel</v-btn>
          <v-btn color="blue darken-1" flat @click="clicked_launch">Create</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
</template>
<script>
export default {
  methods: {
    schedulerFrequencyText () {
      if (this.scheduler_type == 'scheduler') {
        var time = '<time>'
        this.ready = false 

        if (this.e4) {
          time = this.e4.toString()
          this.ready = true 
        }

        this.scheduler_text = 'Scheduled to run @' + time + ' on ' 
        var days = ''

        for (var i=0; i<this.checkbox_dates.length; i++) {
          days+=this.checkbox_dates[i]
          if (i < this.checkbox_dates.length-1) days+=', '
        }
        if (!days) {
          days='<days>'
          this.ready = false
        }

        this.scheduler_text+=days
        this.scheduler_text+='.'
      }
      else if (this.scheduler_type == 'timer') {
        this.ready = false
        this.scheduler_text = 'Scheduled to '
        if (this.timer_repeat) this.scheduler_text+='run every '
        else this.scheduler_text+='run in '
        var freq = '<X>'
        var unit = '<units>'
        if (this.timer_freq) {
          freq = this.timer_freq.toString()
        }
        if (this.timer_freq_unit) {
          unit = this.timer_freq_unit
          this.ready = true
        }

        this.scheduler_text+= freq
        this.scheduler_text+=' '+unit+'.'

      }
    },

    clicked_launch () {
      var msg = {
        route: "bd.@md.slave.task.schedule.add"
      }
      var data = {}
      if (this.scheduler_type == 'scheduler' && this.ready) {
        data['type'] = 'scheduler'
        data['days'] = this.checkbox_dates
        data['clock'] = this.e4.toString()
        console.log("\n\n\n")
        console.log(this.e4.toString())
        console.log("\n\n\n")
      }

      else if (this.scheduler_type == 'timer' && this.ready) {
        data['type'] = 'timer'
        data['frequency_unit'] = this.timer_freq_unit
        data['frequency'] = parseInt(this.timer_freq)
        data['repeat'] = this.timer_repeat
      }

      else {
        console.log("filled out form:" + this.ready)
      }
      console.log("WORKS!")
      if (this.ready) {
        console.log("Sending data: " + JSON.stringify(data))
        msg['data'] = data
        var s = this.$socket.emit('forward', msg)
        if (s.connected) this.dialog = false
      }
    }
  },
  data: () => {
    return {
      ready: false,
      timer_freq_unit: null,
      timer_freq: null,
      timer_repeat: false,
      scheduler_type: 'timer',
      scheduler_text: 'Please fill inputs',
      checkbox_dates:[],
      e4: null,
      dialog: false
    }
  }
}
</script>

